body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background: #fff;
  color: #333;
}

.site-header {
  background-color: #1c6b43;
  color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.site-header nav a {
  color: white;
  margin-left: 1rem;
  text-decoration: none;
}

.site-header nav a:hover {
  text-decoration: underline;
}

.hero {
  text-align: center;
  padding: 4rem 2rem;
  background: #f1f8e9;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #388e3c;
}

.hero p {
  font-size: 1.2rem;
  color: #666;
}

.site-footer {
  background: #f4f4f4;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
  color: #666;
}

@media (max-width: 768px) {
  .site-header {
    flex-direction: column;
    text-align: center;
  }

  .site-header nav a {
    display: block;
    margin: 0.5rem 0;
  }
}

.menu-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
}
.menu-card {
  background: #e8f5e9;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
  transition: background 0.3s;
}
.menu-card:hover {
  background: #c8e6c9;
  font-weight: 600;
}
.event-image {
  max-width: 100%;
  border-radius: 8px;
  margin-bottom: 1rem;
}
.testimonial-nav button {
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
}

.hero {
  text-align: center;
  padding: 2rem 1rem;
}

.hero-image {
  width: 100%;
  max-height: 500px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 2rem;
}

.cta-button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #388E3C;
}
